import axios from "axios";
import React, { useState } from 'react';
import { useForm, useLocalStorage } from '@mantine/hooks';
import { Mail, Lock, UserCircle } from 'tabler-icons-react';
import {
  TextInput,
  PasswordInput,
  Group,
  Checkbox,
  Button,
  Text,
  LoadingOverlay,
  Anchor,
} from '@mantine/core';
import { UserProfile } from './LocalStorage';

export interface AuthenticationFormProps {
  noShadow?: boolean;
  noPadding?: boolean;
  noSubmit?: boolean;
  style?: React.CSSProperties;
  formType: 'register' | 'login';
  setFormType: React.Dispatch<React.SetStateAction<"register" | "login">>;
  modalSetOpened: React.Dispatch<React.SetStateAction<boolean>>;
}


export default function AuthenticationForm({
  noSubmit,
  formType,
  setFormType, 
  modalSetOpened,
}: AuthenticationFormProps) {
  const [, setLogin] = useLocalStorage<UserProfile | null>({ key: 'login', defaultValue: null });
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [registered, setRegistered] = useLocalStorage<boolean>({ key: 'registered', defaultValue: false });

  const toggleFormType = () => {
    setFormType((current) => (current === 'register' ? 'login' : 'register'));
    setError(null);
  };

  const form = useForm({
    initialValues: {
      name: '',
      email: '',
      password: '',
      confirmPassword: '',
      termsOfService: true,
    },

    validationRules: {
      name: (value) => formType === 'login' || value.trim().length >= 2,
      email: (value) => /^\S+@\S+$/.test(value),
      password: (value) => formType === 'login' || /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{6,}$/.test(value),
      confirmPassword: (val, values) => formType === 'login' || val === values?.password,
    },

    errorMessages: {
      email: '   .',
      password: '  1  1    6  .',
      confirmPassword: "  .",
    },
  });

  const signup = (id: string, pw: string, name: string) => {
    const data = {
      name: name,
      email: id,
      password: pw,
    };
    axios.post(
      "http://43.200.180.159:5001/auth/signup",
      data,
      { withCredentials: true}
    )
    .then( () => setRegistered(true) )
    .catch( ( {response} ) => {
      if (response.status === 409) {
        setRegistered(true);
        setError(" . ")
      }
      response.status === 500 && setError(" .  ");
    });
  };

  const login = async (id: string, pw: string) => {
    let user;
    try {
      const response = await axios.post(
        "http://43.200.180.159:5001/auth/login",
        { email: id, password: pw },
        { withCredentials: true}
      );
      
      user = {
        token: response.data.token,
        // firstName: "",
        // lastName: "",
        name: response.data.name,
        email: response.data.email,
        avatar: "",
      };
      setLogin(user);
      setRegistered(false);
    }
    catch (err) {
      user = null;
    }
    return user;
  };

  const handleSubmit = async () => {
    setLoading(true);
    setError(null);
    if (formType === 'register' && !registered ) {
      signup(
        form.values.email,
        form.values.password,
        form.values.name,
      )
    } else {
      const user = await login(form.values.email, form.values.password);
      if (user) {
        modalSetOpened(false);
      } else {
        setError('id    .')
      }
    }
    setLoading(false);
  };

  return (
    <form onSubmit={form.onSubmit(handleSubmit)}>
      <LoadingOverlay visible={loading} />
      { formType === 'register' && !registered && (
        <TextInput
          mt="md"
          required
          placeholder=""
          label=""
          icon={<UserCircle />}
          {...form.getInputProps('name')}
        />
      )}

      <TextInput
        mt="md"
        required
        placeholder=""
        label=""
        icon={<Mail />}
        {...form.getInputProps('email')}
      />

      <PasswordInput
        mt="md"
        required
        placeholder=""
        label=""
        icon={<Lock />}
        {...form.getInputProps('password')}
      />

      {formType === 'register' && !registered && (
        <PasswordInput
          mt="md"
          required
          label=""
          placeholder=""
          icon={<Lock />}
          {...form.getInputProps('confirmPassword')}
        />
      )}

      {formType === 'register' && !registered && (
        <Checkbox
          mt="xl"
          size="xs"
          label=" . "
          {...form.getInputProps('termsOfService', { type: 'checkbox' })}
        />
      )}

      {error && (
        <Text color="red" size="sm" mt="sm">
          {error}
        </Text>
      )}

      {!noSubmit && (
        <Group position="apart" mt="xl">
          <Anchor
            component="button"
            type="button"
            color="gray"
            onClick={toggleFormType}
            size="xs"
          >
            {(formType === 'register' && !registered)
              ? ' ? '
              : "  ? "}
          </Anchor>

          <Button color="blue" type="submit">
            {(formType === 'register' && !registered) ? '' : ''}
          </Button>
        </Group>
      )}
    </form>
  );
}
